import {
  glMatrix,
  mat4,
  quat,
  ReadonlyVec2,
  ReadonlyVec3,
  vec2,
  vec3,
  vec4,
} from "gl-matrix";
import type { Camera, DrawableEntity, DrawObject, DrawProduct, DuoMeasurementValues, ManholeMeasureValues, MeasureEntity, MeasureSettings } from "@novorender/measure-api";
import type { MeasureScene } from "./measureScene";
import { getResultDrawObject, SCREEN_SPACE_EPSILON } from "./drawobject_factory";

export function getPathMatrices(width: number, height: number, camera: Camera): { camMat: mat4; projMat: mat4 } {
  const camMat = mat4.fromRotationTranslation(
    mat4.create(),
    camera.rotation,
    camera.position
  );
  mat4.invert(camMat, camMat);
  if (camera.kind == "pinhole") {
    const projMat = mat4.perspective(
      mat4.create(),
      glMatrix.toRadian(camera.fov),
      width / height,
      camera.near,
      camera.far
    );
    return { camMat, projMat };
  } else {
    const aspect = width / height;
    const halfHeight = camera.fov / 2;
    const halfWidth = halfHeight * aspect;
    const projMat = mat4.ortho(
      mat4.create(),
      -halfWidth,
      halfWidth,
      -halfHeight,
      halfHeight,
      camera.near,
      camera.far
    );
    return { camMat, projMat };
  }
}

export function toScreen(projMat: mat4, width: number, height: number, p: ReadonlyVec3): ReadonlyVec2 {
  const _p = vec4.transformMat4(
    vec4.create(),
    vec4.fromValues(p[0], p[1], p[2], 1),
    projMat
  );

  const pt = vec2.fromValues(
    Math.round(((_p[0] * 0.5) / _p[3] + 0.5) * width),
    Math.round((0.5 - (_p[1] * 0.5) / _p[3]) * height)
  );

  return pt.every((num) => !Number.isNaN(num) && Number.isFinite(num)) ? pt : vec2.fromValues(-100, -100);
};

export function toPathPointsFromMatrices(
  points: ReadonlyVec3[],
  camMat: mat4,
  projMat: mat4,
  near: number,
  width: number,
  height: number,
  ortho: boolean
): { screenPoints: ReadonlyVec2[], points2d: ReadonlyVec2[], removedIndices: number[], addedIndices: number[] } | undefined {
  const clip = (p: vec3, p0: vec3) => {
    const d = vec3.sub(vec3.create(), p0, p);
    vec3.scale(d, d, (-near - p[2]) / d[2]);
    return vec3.add(d, d, p);
  };

  const points2d: ReadonlyVec2[] = [];
  const removedIndices: number[] = [];
  const addedIndices: number[] = [];
  const sv = points.map((v) => vec3.transformMat4(vec3.create(), v, camMat));
  if (ortho) {
    for (const p of sv) {
      if (p[2] > 0 && p[2] < 0.1) {
        p[2] = -0.0001;
      }
    }
  }
  const screenPoints = sv.reduce((tail, head, i) => {
    if (head[2] > SCREEN_SPACE_EPSILON) {
      if (i === 0 || sv[i - 1][2] > 0) {
        removedIndices.push(i);
        return tail;
      }
      const p0 = clip(sv[i - 1], head);
      const _p = toScreen(projMat, width, height, p0);
      points2d.push(_p);
      return tail.concat([_p]);
    }
    const _p = toScreen(projMat, width, height, head);
    points2d.push(_p);
    if (i !== 0 && sv[i - 1][2] > SCREEN_SPACE_EPSILON) {
      const p0 = clip(head, sv[i - 1]);
      const _p0 = toScreen(projMat, width, height, p0);
      addedIndices.push(i);
      return tail.concat([_p0], [_p]);
    }
    return tail.concat([_p]);
  }, [] as ReadonlyVec2[]);
  if (screenPoints.length) {
    return { screenPoints, points2d, removedIndices, addedIndices };
  }
  return undefined;
}


export function FillDrawInfo2D(width: number, height: number, camera: Camera, drawObjects: DrawObject[]) {
  const { camMat, projMat } = getPathMatrices(width, height, camera);

  for (const drawObject of drawObjects) {
    for (const drawPart of drawObject.parts) {
      const points = toPathPointsFromMatrices(
        drawPart.vertices3D,
        camMat,
        projMat,
        camera.near,
        width,
        height,
        camera.kind == "orthographic"
      );
      if (points) {
        const { screenPoints, removedIndices, addedIndices } = points;
        drawPart.vertices2D = screenPoints;
        if ((removedIndices.length > 0 || addedIndices.length > 0)) {
          if (drawPart.text && Array.isArray(drawPart.text)) {
            drawPart.text[0] = drawPart.text[0].reduce((tail, head, i) => {
              if (addedIndices.find((v) => v == i) != undefined && removedIndices.find((v) => v == i - 1) == undefined) {
                return tail.concat(["", head])
              }
              if (removedIndices.find((v) => v == i) != undefined && removedIndices.find((v) => v == i + 1) != undefined) {
                return tail;
              } else {
                return tail.concat(head);
              }
            }, [] as string[]);
          }
          const newVert3d: ReadonlyVec3[] = [];
          drawPart.vertices3D.forEach((v, i) => {
            if (addedIndices.find((v) => v == i) != undefined && removedIndices.find((v) => v == i - 1) == undefined) {
              newVert3d.concat([vec3.create(), v])
            }
            else if (removedIndices.find((v) => v == i) == undefined || removedIndices.find((v) => v == i + 1) == undefined) {
              return newVert3d.push(v);
            }
            drawPart.vertices3D = newVert3d;
          });
        }
      }
      if (drawPart.voids) {
        drawPart.voids.forEach((drawVoid, j) => {
          const voidPoints = toPathPointsFromMatrices(
            drawVoid.vertices3D,
            camMat,
            projMat,
            camera.near,
            width,
            height,
            camera.kind == "orthographic"
          );
          if (voidPoints) {
            const { screenPoints, removedIndices, addedIndices } = voidPoints;
            drawVoid.vertices2D = screenPoints;
            if ((removedIndices.length > 0 || addedIndices.length > 0) && drawPart.text && Array.isArray(drawPart.text)) {
              drawPart.text[j + 1] = drawPart.text[j + 1].reduce((tail, head, i) => {
                if (addedIndices.find((v) => v == i) != undefined && removedIndices.find((v) => v == i - 1) == undefined) {
                  return tail.concat(["", head])
                }
                if (removedIndices.find((v) => v == i) != undefined && removedIndices.find((v) => v == i + 1) != undefined) {
                  return tail;
                } else {
                  return tail.concat(head);
                }
              }, [] as string[]);
            }

          }
        });
      }
    }
  }
}


export async function renderMeasureEntity(
  width: number, height: number, camera: Camera,
  scene: MeasureScene,
  entity: DrawableEntity,
  setting?: MeasureSettings
): Promise<(DrawProduct | undefined) | undefined> {
  if (entity) {
    let drawObjects: DrawObject[] = [];
    let kind: "manhole" | "basic" | "measureResult" | undefined = undefined;
    if (entity.drawKind == "manhole") {
      drawObjects = await scene.getManholeDrawObject(entity as ManholeMeasureValues);
      kind = "manhole"
    }
    else if (entity.drawKind == "measureResult") {
      drawObjects = [getResultDrawObject(entity as DuoMeasurementValues)];
      kind = "measureResult";
    }
    else {
      const drawObject = await scene.getEntitiyDrawObjects(entity as MeasureEntity, setting);
      if (drawObject) {
        drawObjects = [drawObject];
      }
      kind = "basic";
    }

    if (drawObjects) {
      FillDrawInfo2D(width, height, camera, drawObjects);
    }
    return {
      kind, objects: drawObjects
    };
  }
  return undefined;
}