import {
  glMatrix,
  mat4,
  ReadonlyMat4,
  ReadonlyVec2,
  ReadonlyVec3,
  vec3,
  vec4,
} from "gl-matrix";
import {
  equalMeasureEntityIndex,
  MeasureEntity,
  MeasureEntityIndex,
  PathKind,
} from "./measureEntity";
import { getPathMatrices, toPathPointsFromMatrices } from "./pathRender";
import type { DrawPart, MeasureSettings, ObjectId } from "@novorender/measure-api";
glMatrix.setMatrixArrayType(Array);

export interface Path2dInfo {
  readonly originalIndex: number;
  readonly path: Path2D;
  readonly kind: PathKind;
  readonly instanceIndex: number;
}

function getDir(viewWorldMatrix: ReadonlyMat4) {
  const dir = vec4.fromValues(0, 0, 1, 0);
  vec4.transformMat4(dir, dir, viewWorldMatrix);
  return vec3.fromValues(dir[0], dir[1], dir[2]);
}

interface RenderOutputInterface {
  readonly worldClipMatrix: ReadonlyMat4;
  readonly viewClipMatrix: ReadonlyMat4;
  readonly worldViewMatrix: ReadonlyMat4;
}

export class MeasureObject {
  private _renderOutput: RenderOutputInterface | undefined;
  private facePaths: readonly Path2dInfo[] = [];
  private edgePaths: readonly Path2dInfo[] = [];
  private currentDir = vec3.create();
  private viewDirMatrix = mat4.create();

  selected: MeasureEntityIndex | undefined;

  highlighted: MeasureEntityIndex | undefined;
  dirty = true;

  constructor(
    readonly id: ObjectId,
    readonly getPaths: (
      worldViewMatrix: ReadonlyMat4
    ) => Promise<readonly Path2dInfo[]>,
    readonly getEntitiyDrawObjects: (
      enitity: MeasureEntity,
      setting?: MeasureSettings
    ) => Promise<
      {
        vertices: ReadonlyVec3[];
        drawType: "lines" | "filled" | "vertex" | "curveSegment";
        elevation?: { from: number; to: number; horizontalDisplay: boolean };
      }[]
    >,
    readonly swapCylinderInternal: (
      entity: MeasureEntity,
      to: "inner" | "outer"
    ) => Promise<number | undefined>,
    selectedEntity: MeasureEntityIndex | undefined
  ) {
    this.selected = selectedEntity;
  }

  get selectedEntity(): MeasureEntity | undefined {
    if (this.selected) {
      return this.createMeasureEntity(this.selected);
    }
  }

  get productId(): ObjectId {
    return this.id;
  }

  /** Returns a draw object with 2d screen coordinates*/
  async swapCylinder(to: "inner" | "outer"): Promise<boolean> {
    const entity = this.selectedEntity;
    if (entity) {
      const faceIdx = await this.swapCylinderInternal(
        this.createMeasureEntity(entity),
        to
      );
      if (faceIdx != undefined) {
        this.selected!.pathIndex = faceIdx;
      }
    }

    return false;
  }

  createMeasureEntity(index: MeasureEntityIndex): MeasureEntity {
    const kind = index.pathKind;
    return { ...index, ObjectId: this.id, kind };
  }

  contextTransform(
    context: CanvasRenderingContext2D,
    width: number,
    height: number,
    renderOutput: RenderOutputInterface
  ) {
    const halfWidth = width / 2;
    const halfHeight = height / 2;
    const scale = renderOutput.viewClipMatrix[5] * halfHeight;
    var translateX = (1 + renderOutput.worldClipMatrix[12]) * halfWidth;
    var translateY = (1 - renderOutput.worldClipMatrix[13]) * halfHeight;
    const mat = new DOMMatrix([scale, 0, 0, -scale, translateX, translateY]);
    context.setTransform(mat);
    context.lineWidth = 2 / scale;
  }

  renderPoints(
    context: CanvasRenderingContext2D,
    width: number,
    height: number,
    points: vec3[]
  ) {
    if (this._renderOutput) {
      const renderOutput = this._renderOutput;
      this.contextTransform(context, width, height, renderOutput);
      const scale = (renderOutput.viewClipMatrix[5] * height) / 2;
      const { viewDirMatrix } = this;

      const rad = 5 / scale;
      context.fillStyle = "red";
      for (const point of points) {
        const p = vec3.transformMat4(vec3.create(), point, viewDirMatrix);
        const [x, y] = p;
        context.beginPath();
        context.ellipse(x, y, rad, rad, 0, 0, 2 * Math.PI);
        context.fill();
      }
      context.resetTransform();
    }
  }

  renderPaths(
    context: CanvasRenderingContext2D,
    width: number,
    height: number
  ) {
    const { facePaths, edgePaths } = this;
    const { pathIndex: highlightedIndex, pathKind: highlightedKind } =
      this.highlighted ?? {};
    const { pathIndex: selectedIndex, pathKind: selectedKind } =
      this.selected ?? {};

    if (this._renderOutput) {
      this.contextTransform(context, width, height, this._renderOutput);

      for (const pathInfo of facePaths) {
        const { path, originalIndex } = pathInfo;
        if (highlightedKind == "face" && originalIndex === highlightedIndex) {
          context.fillStyle = "yellow";
        } else if (selectedKind == "face" && originalIndex === selectedIndex) {
          context.fillStyle = "green";
        } else {
          context.fillStyle = "darkgray";
        }
        context.fill(path);
        context.strokeStyle = "white";
        context.stroke(path);
      }

      for (const pathInfo of edgePaths) {
        const { path, originalIndex } = pathInfo;

        if (highlightedKind == "edge" && originalIndex === highlightedIndex) {
          context.strokeStyle = "yellow";
        } else if (selectedKind == "edge" && originalIndex === selectedIndex) {
          context.strokeStyle = "green";
        } else {
          context.strokeStyle = "black";
        }
        context.stroke(path);
      }

      context.resetTransform();
    }
    this.dirty = false;
  }

  hover(
    context: CanvasRenderingContext2D,
    x: number,
    y: number,
    width: number,
    height: number
  ): boolean {
    const idx = this.pickPath(context, x, y, width, height);
    if (equalMeasureEntityIndex(this.highlighted, idx)) {
      return false;
    }
    this.highlighted = idx;
    this.dirty = true;
    return true;
  }

  select(): boolean {
    if (
      equalMeasureEntityIndex(this.highlighted, this.selected) ||
      !this.highlighted
    ) {
      return false;
    }
    this.selected = this.highlighted;
    this.dirty = true;
    return true;
  }

  private pickPath(
    context: CanvasRenderingContext2D,
    x: number,
    y: number,
    width: number,
    height: number
  ): MeasureEntityIndex | undefined {
    if (this._renderOutput) {
      this.contextTransform(context, width, height, this._renderOutput);

      const { facePaths, edgePaths } = this;

      for (let i = edgePaths.length - 1; i >= 0; --i) {
        const pathInfo = edgePaths[i];
        const { path } = pathInfo;
        if (context.isPointInStroke(path, x, y)) {
          return {
            pathKind: "edge",
            pathIndex: pathInfo.originalIndex,
            instanceIndex: pathInfo.instanceIndex,
            parameter: 0,
          };
        }
      }

      for (let i = facePaths.length - 1; i >= 0; --i) {
        const pathInfo = facePaths[i];
        const { path } = pathInfo;
        if (context.isPointInPath(path, x, y)) {
          return {
            pathKind: "face",
            pathIndex: pathInfo.originalIndex,
            instanceIndex: pathInfo.instanceIndex,
            parameter: 0,
          };
        }
      }
    }
    context.resetTransform();
  }
}
