import * as Comlink from "comlink";
import { MeasureScene } from "./measureScene";
import type { Service } from "./worker";
import { ReadonlyVec2, ReadonlyVec3, vec2, vec3 } from "gl-matrix";
import { FillDrawInfo2D, getPathMatrices, renderMeasureEntity, toPathPointsFromMatrices, toScreen } from "./pathRender";
import type * as Public from "@novorender/webgl-api";
import type { Camera, DrawableEntity, DrawObject, DrawProduct, MeasureSettings } from "@novorender/measure-api";
import { get2dNormal, getDrawObjectFromPointArray, getDrawTextObject, getTraceDrawOject, SCREEN_SPACE_EPSILON } from "./drawobject_factory";

type MeasureService = Comlink.Remote<Service>;

const currentScriptUrl =
  (document.currentScript as HTMLScriptElement | null)?.src ?? import.meta.url;

/** Measure api loads from same scene assets. Brep files are required*/
export class MeasureAPI {
  private readonly scriptUrl: string;

  private workers:
    | {
      readonly measure: {
        worker: Comlink.Endpoint;
        service: MeasureService;
      };
    }
    | undefined;

  constructor(scriptBaseUrl?: string) {
    const url = new URL(scriptBaseUrl ?? currentScriptUrl);
    this.scriptUrl = new URL("./", url).href.slice(0, -1);
  }

  private createWorkers() {
    const createWorker = (url: string, name: string): Comlink.Endpoint => {
      if (this.scriptUrl.startsWith(self.location.origin)) {
        return new Worker(url, { type: "classic", name });
      } else {
        const scriptBlob = new Blob(
          [`importScripts(${JSON.stringify(url)});`],
          { type: "text/javascript" }
        );
        const blobUrl = URL.createObjectURL(scriptBlob);
        const worker = new Worker(blobUrl, { type: "classic", name });
        URL.revokeObjectURL(blobUrl);
        return worker;
      }
    };
    const measureWorker = createWorker(
      `${this.scriptUrl}/worker.js`,
      "Measure"
    );
    const measureService = Comlink.wrap<Service>(measureWorker);
    this.workers = {
      measure: {
        worker: measureWorker,
        service: measureService,
      },
    };
    measureService.initialize(this.scriptUrl);
  }

  loadScene(url: URL | string) {
    if (!this.workers) {
      this.createWorkers();
    }
    if (typeof url === "string") {
      url = new URL(url.toString());
    }
    return new MeasureScene(url, this.workers!.measure.service);
  }

  async dispose(): Promise<void> {
    const { workers } = this;
    if (workers) {
      const { measure } = workers;
      await measure.service.terminate();
      measure.service[Comlink.releaseProxy]();
      this.workers = undefined;
    }
  }

  /** Converts world space points to on screen pixel path and points*/
  toPathPoints(
    points: ReadonlyVec3[],
    width: number, height: number, camera: Camera,
  ): { screenPoints: ReadonlyVec2[], points2d: ReadonlyVec2[], removedIndices: number[] } | undefined {
    const { camMat, projMat } = getPathMatrices(width, height, camera);

    return toPathPointsFromMatrices(
      points,
      camMat,
      projMat,
      camera.near,
      width, height,
      camera.kind == "orthographic"
    );
  }

  /** Converts world space points to on screen pixel points. Input and output are of equal length. */
  toMarkerPoints(width: number, height: number, camera: Camera, points: ReadonlyVec3[]): (ReadonlyVec2 | undefined)[] {
    const { camMat, projMat } = getPathMatrices(width, height, camera);

    return points
      .map((p) => vec3.transformMat4(vec3.create(), p, camMat))
      .map((p, i, arr) => {
        if (camera.kind === "orthographic") {
          if (p[2] > 0 && p[2] < 0.1) {
            p[2] = -0.0001;
          }
        }

        if (p[2] > SCREEN_SPACE_EPSILON) {
          return undefined;
        }

        return toScreen(projMat, width, height, p)
      })
  }

  async getDrawMeasureEntity(
    width: number, height: number, camera: Camera,
    scene: MeasureScene,
    entity: DrawableEntity,
    setting?: MeasureSettings
  ): Promise<(DrawProduct | undefined) | undefined> {
    return renderMeasureEntity(width, height, camera, scene, entity, setting);
  }

  updateDrawProuct(width: number, height: number, camera: Camera, drawProduct: DrawProduct) {
    FillDrawInfo2D(width, height, camera, drawProduct.objects);
  }

  getDrawObjectFromPoints(width: number, height: number, camera: Camera, points: ReadonlyVec3[], closed = true, angles = true, generateLineLabels = false): DrawProduct | undefined {
    return getDrawObjectFromPointArray(width, height, camera, points, closed, angles, generateLineLabels);
  }

  getDrawText(width: number, height: number, camera: Camera, points: ReadonlyVec3[], text: string): DrawProduct | undefined {
    return getDrawTextObject(width, height, camera, points, text);
  }

  traceDrawObjects(objects: DrawProduct[], line: { start: ReadonlyVec2, end: ReadonlyVec2 }): DrawProduct {
    return getTraceDrawOject(objects, line);
  }

  get2dNormal(object: DrawProduct, line: { start: ReadonlyVec2, end: ReadonlyVec2 }): { normal: ReadonlyVec2, position: ReadonlyVec2 } | undefined {
    return get2dNormal(object, line);
  }


}
