import type { ObjectId } from "@novorender/measure-api";
import type { ReadonlyVec2, ReadonlyVec3 } from "gl-matrix";

export type PathKind = "face" | "edge" | "vertex" | "curveSegment";

export interface MeasureEntity extends MeasureEntityIndex {
    ObjectId: ObjectId;
    kind: "edge" | "face" | "vertex" | "curveSegment";
}

export interface MeasureEntityIndex {
    pathKind: PathKind;
    pathIndex: number;
    instanceIndex: number;
    parameter: number | ReadonlyVec2 | ReadonlyVec3;
}

export function equalMeasureEntityIndex(a?: MeasureEntityIndex, b?: MeasureEntityIndex) {
    if ((!a && b) || (a && !b)) {
        return false;
    }
    if (a && b) {
        return a.pathIndex == b.pathIndex && a.pathKind == b.pathKind;
    }
    return true;
}

export function equalMeasureEntity(a?: MeasureEntity, b?: MeasureEntity) {
    if ((!a && b) || (a && !b)) {
        return false;
    }
    if (a && b) {
        return a.ObjectId == b.ObjectId && a.pathIndex == b.pathIndex && a.pathKind == b.pathKind;
    }
    return true;
}
